.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navMain {
  position: fixed;
  left: 0;
  top: 0;
  box-sizing: border-box;
  height: 100vh;
  width: 12vw;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.navList {
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0;
}

.listItem {
  background-color: #bfbfbf;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  margin: auto;
  cursor: pointer;
  transition: 0.3s;
}

.listItem-active {
  background-color: #5cb1ff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  margin: auto;
  cursor: pointer;
  transition: 0.3s;
}

.navImg {
  height: 40px;
  width: 40px;
}

.navImg1 {
  height: 24px;
  width: 24px;
}

.userImgMain {
  width: 100%;
  height: 25vh;
  text-align: center;
  margin-top: 12vh;
}

.userImg {
  height: 12vw;
}

