.ScrollbarDiv{
  overflow-y: scroll;
  white-space: no-wrap ;
}

.ScrollbarDiv::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.ScrollbarDiv::-webkit-scrollbar-track {
  border-radius: 10px;
}
 
/* Handle */
.ScrollbarDiv::-webkit-scrollbar-thumb {
  background:rgb(173, 170, 170); 
  border-radius: 5px;
}

/* Handle on hover */
.ScrollbarDiv::-webkit-scrollbar-thumb:hover {
  background: #b7b7b7; 
}

